/* MEDIA QUERY FOR SMALLER DESKTOP SCREENS AND SMALLER */
@media screen and (max-width: 980px) {

    /* this will be appied on any screen smaller than 980px */
    header {
        padding-bottom: 0;
        justify-content: center;
        position: relative;
    }

    header h1 {
        width: 100%;
        text-align: center;
    }

    header nav ul {
        margin-top: 20px;
        width: 100%;
        justify-content: center;
    }

    header nav ul li a {
        font-size: 20px;
    }

    footer h2,
    footer div {
        text-align: center;
        width: 100%;
    }

    .hero,
    .hero-text {
        width: 100%;
    }

    /* .hero img {
              width: 50%;
              height: 50%;
          } */

    .hero-text {
        text-align: center;
    }

    .section-title {
        width: 80%;
    }

    .project-row {
        flex: 0 70%;
    }
}

.section-contact {
    flex: 0 100%;
}

/* MEDIA QUERY FOR TABLETS AND SMALLER */
@media screen and (max-width: 768px) {

    /* this will be applied on any screen between 768px and 575px */
    .section-about {
        padding: 30px 15px;
    }

    .section-title h3 {
        flex: 1 100%;
        text-align: center;
    }

    .project-content {
        flex: 2 100%;
        text-align: center;
        justify-content: center;
    }

    .project-content img {
        flex: 0 32%;
        margin-right: 0;
        margin-top: 15px;
        margin-bottom: 15px;
    }

    .project-text {
        flex: 100%;
    }
}

/* MEDIA QUERY FOR MOBILE PHONES AND SMALLER */
@media screen and (max-width: 575px) {

    /* this will be applied on any screen smaller than 575px */
    .section-title {
        width: 95%;
    }

    .project-row {
        flex: 0 100%;
    }
}